.page {
  font-family: "Inter", sans-serif;
  color: $main-color;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 120px;
  }

  &__section-title {
    margin-bottom: 48px;

    font-weight: 700;
    font-size: 32px;
    line-height: 32px;

    text-align: center;
    letter-spacing: -1.5px;

    @include onDesktop {
      font-size: 48px;
      line-height: 48px;
    }
  }

  &__header {
    margin-bottom: 120px;
  }
}
